.home {
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 18px;
    grid-row-gap: 40px;
    justify-items: center;
    margin-bottom: 80px;

    .card {
      max-width: 80%;
      width: 100%;
      // border: 1px solid black;
      box-shadow: 0px 0px 15px 2px rgba(34, 60, 80, 0.1);
      border-radius: 20px;
      color: #616060;

      &__inner-title {
        border-bottom: 3px solid #616060;
        padding-bottom: 15px;
      }
      &__inner-descr {
        text-align: center;
        padding: 20px 27px;
      }

      &__inner-img {
        max-width: 225px;
        width: 225px;
      }
    }
  }
}
